/* index.css */
/* Глобальный сброс стилей и базовые настройки для более легкого и воздушного вида */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #f7f9fc; /* светлый, нейтральный фон */
  color: #333;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  list-style: none;
}

/* Можно добавить дополнительные глобальные настройки здесь */